import React from 'react';
import {Link} from 'react-router-dom';
import * as Pages from '../../../router/Pages';
import Save from '../actions/Save';
import {useDispatch, useSelector} from "react-redux";

const Ua = () => {

  const dispatch = useDispatch()
  const owner = useSelector(state => state.App.owner)

  const setViewed = () => {
    dispatch(Save({
      isFirstLoginGreetingViewed: true
    }))
  }

  const nameSuffix = owner && owner.user ? `, ${owner.user.firstName}` : ''

  return <>
    <h1 className="mb-3">{nameSuffix}!</h1>
    <p>       .</p>
    <p> 1.   
      &nbsp;<Link to={Pages.MY_COMPANY} onClick={setViewed}> </Link>
      &nbsp;    .</p>
    <p> 2.   
      &nbsp;<Link to={Pages.STORES} onClick={setViewed}> </Link>
      &nbsp;       .</p>
    <p> 3.     -   
      &nbsp;<Link to={Pages.STAFF} onClick={setViewed}></Link>
      &nbsp;     .</p>
    <p className="mt-3">  !</p>

    <div className="text-center">
      <Link className="btn btn-success btn-lg"
            onClick={setViewed}
            to={Pages.HOME}>!</Link>
    </div>
  </>
}

const Greeting = () => {

  const locale = useSelector(state => state.App.locale)

  let content
  if (locale === 'ua') {
    content = <Ua/>
    // } else if (locale === 'ru') {
    //   content = <Ru/>
  } else {
    content = <Ua/>
  }

  return <div className="container py-5">
    <div className="row">
      <div className="col-12 col-md-8 mx-auto">

        {content}

      </div>
    </div>
  </div>

}
export default Greeting
